html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: "Spartan", sans-serif;
  width: 100%;
  height: 100vh;
  background: #3a4764;
}
body.cont-tema2 {
  background: #e6e6e6;
}
body.cont-tema3 {
  background: #160628;
}
body a {
  color: #fff;
  text-decoration: none;
  font-weight: 700;
}

.secciones {
  border-radius: 2rem;
}

.contenedor {
  width: 90%;
  height: 100vh;
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  transition: all 0.6s ease;
}
body .calculadora {
  width: 45%;
  height: 70%;
}
@media (max-width: 1280px) {
  body .calculadora {
    width: 50%;
  }
}
@media (max-width: 960px) {
  body .calculadora {
    width: 75%;
  }
}
@media (max-width: 768px) {
  body .calculadora {
    width: 80%;
  }
}
@media (max-width: 480px) {
  body .calculadora {
    width: 98%;
    height: 60%;
  }
}
body .calculadora .header {
  height: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  padding: 0 2.4rem;
}
@media (max-width: 768px) {
  body .calculadora .header {
    padding: 0 1rem;
  }
}
@media (max-width: 1280px) {
  body .calculadora .header h1 {
    font-size: 2.4rem;
  }
}
@media (max-width: 768px) {
  body .calculadora .header h1 {
    font-size: 1.6rem;
  }
}
body .calculadora .header .temas {
  width: 30%;
  display: flex;
}
@media (max-width: 1280px) {
  body .calculadora .header .temas {
    width: 35%;
  }
}
@media (max-width: 960px) {
  body .calculadora .header .temas {
    width: 30%;
  }
}
@media (max-width: 768px) {
  body .calculadora .header .temas {
    width: 40%;
  }
}
@media (max-width: 480px) {
  body .calculadora .header .temas {
    width: 50%;
  }
}
body .calculadora .header .temas h3 {
  display: flex;
  align-items: flex-end;
}
body .calculadora .header .temas .caja-tema {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  height: 3rem;
  position: relative;
  background: #232c43;
  align-items: center;
  margin-left: 2rem;
  border-radius: 2rem;
}
body .calculadora .header .temas .caja-tema .circulo {
  position: absolute;
  width: 2rem;
  height: 2rem;
  background: #d03f2f;
  border-radius: 50%;
  left: 5%;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 480px) {
  body .calculadora .header .temas .caja-tema .circulo {
    left: 8%;
  }
}
body .calculadora .header .temas .caja-tema .circulo.tema2 {
  left: 39%;
}
@media (max-width: 1280px) {
  body .calculadora .header .temas .caja-tema .circulo.tema2 {
    left: 37%;
  }
}
@media (max-width: 768px) {
  body .calculadora .header .temas .caja-tema .circulo.tema2 {
    left: 35%;
  }
}
body .calculadora .header .temas .caja-tema .circulo.tema3 {
  left: 71%;
}
@media (max-width: 1280px) {
  body .calculadora .header .temas .caja-tema .circulo.tema3 {
    left: 65%;
  }
}
@media (max-width: 768px) {
  body .calculadora .header .temas .caja-tema .circulo.tema3 {
    left: 60%;
  }
}
@media (max-width: 480px) {
  body .calculadora .header .temas .caja-tema .circulo.tema3 {
    left: 65%;
  }
}
body .calculadora .header .temas .caja-tema label {
  position: absolute;
  font-weight: 700;
  display: block;
  top: -2.4rem;
}
body .calculadora .header .temas .caja-tema input[type=radio] {
  visibility: hidden;
}
body .calculadora .pantalla {
  margin: 3rem 0;
  height: 20%;
  background: #182034;
}
@media (max-width: 768px) {
  body .calculadora .pantalla {
    margin: 1.5rem 0;
  }
}
body .calculadora .teclado {
  height: 70%;
  background: #232c43;
}
body .pantalla {
  padding: 4.2rem;
  color: #fff;
  font-size: 5.4rem;
  font-weight: 700;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
@media (max-width: 768px) {
  body .pantalla {
    font-size: 4.2rem;
    padding: 3.2rem;
  }
}
@media (max-width: 480px) {
  body .pantalla {
    font-size: 3.2rem;
  }
}
body .teclado {
  padding: 4.2rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-template-areas: "num7 num8 num9 del" "num4 num5 num6 suma" "num1 num2 num3 resta" "punto cero div multi" "resett resett igual igual";
  gap: 1rem;
}
@media (max-width: 768px) {
  body .teclado {
    padding: 2rem;
  }
}
body .teclado .cont-botones {
  background: #eae3dc;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 2rem;
  font-size: 2.4rem;
  font-weight: 700;
  color: #444b5a;
  cursor: pointer;
}
@media (max-width: 768px) {
  body .teclado .cont-botones {
    border-radius: 1rem;
    font-size: 1.8rem;
  }
}
body .teclado .cont-uno {
  grid-area: num1;
}
body .teclado .cont-dos {
  grid-area: num2;
}
body .teclado .cont-tres {
  grid-area: num3;
}
body .teclado .cont-cuatro {
  grid-area: num4;
}
body .teclado .cont-cinco {
  grid-area: num5;
}
body .teclado .cont-seis {
  grid-area: num6;
}
body .teclado .cont-siete {
  grid-area: num7;
}
body .teclado .cont-ocho {
  grid-area: num8;
}
body .teclado .cont-nueve {
  grid-area: num9;
}
body .teclado .cont-cero {
  grid-area: cero;
}
body .teclado .cont-punto {
  grid-area: punto;
}
body .teclado .cont-suma {
  grid-area: suma;
}
body .teclado .cont-resta {
  grid-area: resta;
}
body .teclado .cont-multi {
  grid-area: multi;
}
body .teclado .cont-division {
  grid-area: div;
}
body .teclado .cont-borrar {
  grid-area: del;
  background: #637097;
  color: #fff;
}
body .teclado .cont-reset {
  grid-area: resett;
  background: #637097;
  color: #fff;
}
body .teclado .cont-igual {
  grid-area: igual;
  background: #d03f2f;
  color: #fff;
}

